import React from "react";
import CabinetTop from "../../../components/cabinetTop";
import {
  Box,
  Button,
  Container,
  Grid,
  makeStyles,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { useHistory, useLocation } from "react-router";
import { Route } from "react-router-dom";
import {
  getProfileData,
  getSecurityParametrs,
} from "../../../redux/reducers/myProfile";
import { useDispatch, useSelector } from "react-redux";
import GoogleAuthenticator from "./Security/GoogleAuthenticator";
import Edit from "./Security/Edit";
const Profile = React.lazy(() => import("./Profile"));
const Security = React.lazy(() => import("./Security"));
const Verification = React.lazy(() => import("./Verification"));

const Settings = ({ t, i18n }) => {
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.up("lg"));
  const location = useLocation();
  const history = useHistory();
  const classes = useStyles();
  const dispatch = useDispatch();

  React.useEffect(() => {
    dispatch(getProfileData());
    dispatch(getSecurityParametrs());
    // eslint-disable-next-line
  }, []);

  const { securityParameters } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  return (
    <section>
      <Container>
        <CabinetTop title={t("Settings")} />
        <Grid container>
          <Grid item lg={3} xl={3} xs={12} style={{ marginBottom: !lg && 23 }}>
            <ul className="react-tabs__tab-list">
              <Button
                variant="contained"
                className={
                  location.pathname === "/dashboard/settings/profile"
                    ? classes.button_active
                    : classes.button
                }
                size="large"
                onClick={() => history.push("/dashboard/settings/profile")}
                color="primary"
              >
                {t("ProfileSettings")}
              </Button>
              {/* <Button
                variant='contained'
                className={
                  location.pathname === "/dashboard/settings/PaymentDetails"
                    ? classes.button_active
                    : classes.button
                }
                size='large'
                onClick={() =>
                  history.push("/dashboard/settings/PaymentDetails")
                }
                color='primary'
              >
                Платежные реквизиты
              </Button> */}

              <Button
                variant="contained"
                className={
                  location.pathname === "/dashboard/settings/verification"
                    ? classes.button_active
                    : classes.button
                }
                size="large"
                onClick={() => history.push("/dashboard/settings/verification")}
                color="primary"
              >
                {t("VerificationAccount")}
              </Button>

              <Button
                variant="contained"
                className={
                  location.pathname.includes("/dashboard/settings/security")
                    ? classes.button_active
                    : classes.button
                }
                size="large"
                onClick={() => history.push("/dashboard/settings/security")}
                color="primary"
              >
                {t("Security")}
              </Button>
            </ul>
          </Grid>
          <Grid item lg={9} xl={9} xs={12}>
            <Box
              ml={lg && "16px"}
              style={{ width: "100%", minHeight: "100vh" }}
            >
              <Route
                exact
                path="/dashboard/settings/profile"
                render={() => <Profile t={t} />}
              />
              <Route
                path="/dashboard/settings/verification"
                render={() => <Verification t={t} i18n={i18n} />}
              />
              <Route
                exact
                path="/dashboard/settings/security"
                render={() => <Security t={t} i18n={i18n} />}
              />
              <Route
                exact
                path="/dashboard/settings/security/2fa"
                render={() => <GoogleAuthenticator t={t} i18n={i18n} />}
              />
              {securityParameters && (
                <Route
                  path="/dashboard/settings/security/2fa/edit"
                  render={() => <Edit t={t} i18n={i18n} />}
                />
              )}
            </Box>
          </Grid>
        </Grid>
      </Container>
    </section>
  );
};

export default Settings;

const useStyles = makeStyles((theme) => ({
  button: {
    marginBottom: 16,
    width: "100%",
    background: "transparent",
    color: "#B8C0CC",

    "&:hover": {
      "& .MuiButton-label": {
        color: "#000",
      },
    },
  },

  button_active: {
    marginBottom: 16,
    width: "100%",
    background: "#FCE34D",
    color: "#000",
  },

  box: {
    background: "#fff",
    boxShadow: " 0px 1px 0px #E2E8F0",
    borderRadius: 13,
    display: "flex",
    flexDirection: "column",
    padding: "16px!important",
  },
}));
