import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import {
  Button,
  CircularProgress,
  makeStyles,
  Snackbar,
  TextField,
  Typography,
} from "@material-ui/core";
import { Alert, Box } from "@mui/material";
import copyIcon from "../../../../assets/images/state/document-copy.svg";
import { useDispatch, useSelector } from "react-redux";
import {
  enable2fa,
  generateCode,
  setData,
} from "../../../../redux/reducers/2fa";
import CopyToClipboard from "react-copy-to-clipboard";
import { Form, Formik } from "formik";
import { useHistory } from "react-router";
import { MaskCustom } from "../../../../components/inputMask/unicalMask";

const GoogleAuthenticator = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();

  const [copy, setCopy] = useState(false);
  const [loading, setLoading] = useState(false);
  const firstUpdate = useRef(true);

  const { generatedCode, response, isLoading } = useSelector(
    ({ f2aReducer }) => f2aReducer
  );

  useEffect(() => {
    dispatch(generateCode());
    // eslint-disable-next-line
  }, []);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    // eslint-disable-next-line
    if (response?.severity == 200) {
      setLoading(false);
      setTimeout(() => {
        dispatch(setData(null));
        history.push("/dashboard/settings/security");
      }, 1000);
    } else {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [response]);

  return (
    <div style={{ color: "#000" }}>
      <Snackbar
        open={copy || response?.open}
        autoHideDuration={copy ? 800 : 1000}
        onClose={() => {
          if (response?.open) {
            dispatch(setData({ ...response, open: false }));
          } else setCopy(false);
        }}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          severity={
            response?.severity === 200 ? "success" : copy ? "success" : "error"
          }
          color={
            response?.severity === 200 ? "success" : copy ? "success" : "error"
          }
          variant="filled"
        >
          {response?.open ? (
            response?.message?.messages ? (
              response?.message?.messages
            ) : (
              t("success")
            )
          ) : copy ? (
            <>{t("copied")}!</>
          ) : (
            ""
          )}
        </Alert>
      </Snackbar>

      <Box className={classes.wrapper}>
        <Typography className={classes.title} variant="h5">
          {t("SecurityTitle")}
        </Typography>

        <Typography variant="h5" className={classes.subtitle}>
          {t("SecurityText")}
        </Typography>
        {isLoading ? (
          <Box
            sx={{
              height: 302,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <CircularProgress color="primary" />
          </Box>
        ) : (
          <Formik
            initialValues={{}}
            onSubmit={(values) => {
              setLoading(true);
              values["2fa_secret"] = generatedCode?.security?.secret;
              dispatch(enable2fa(values));
            }}
          >
            {({ values, handleChange }) => (
              <Form>
                <Box className={classes.items}>
                  <img
                    src={generatedCode?.security?.qr_code}
                    className={classes.img}
                    alt=""
                  />
                  <Box className={classes.box}>
                    <Typography variant="body1" className={classes.subtitle}>
                      {t("SecurityDescription")}
                    </Typography>
                    <Box className={classes.innerItems}>
                      <CopyToClipboard text={generatedCode?.security?.secret}>
                        <Button
                          size="large"
                          className={classes.buttonCopy}
                          variant="contained"
                          onClick={() => setCopy(true)}
                        >
                          <img className={classes.icon} src={copyIcon} alt="" />
                          {t("copy")}
                        </Button>
                      </CopyToClipboard>
                      <Typography className={classes.code}>
                        {generatedCode?.security?.secret}
                      </Typography>
                    </Box>
                    <TextField
                      type="number"
                      required
                      id={"000000"}
                      InputProps={{
                        inputComponent: MaskCustom,
                      }}
                      name="2fa_otp"
                      onChange={handleChange}
                      value={values["2fa_otp"]}
                      className={classes.SecurityInput}
                      label={t("LabelSecurityCode")}
                      variant="outlined"
                    />
                  </Box>
                </Box>
                <Button
                  className={classes.button}
                  variant="contained"
                  size="large"
                  disabled={loading}
                  color="primary"
                  type="submit"
                >
                  {loading ? (
                    <CircularProgress color="secondary" />
                  ) : (
                    t("enable")
                  )}
                </Button>
              </Form>
            )}
          </Formik>
        )}
      </Box>
    </div>
  );
};

export default GoogleAuthenticator;

const useStyles = makeStyles((theme) => ({
  wrapper: {
    background: "#FFFFFF",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: 8,
    padding: 32,
    [theme.breakpoints.down("sm")]: {
      padding: 16,
    },
  },

  icon: {
    marginRight: 8,
  },

  img: {
    minWidth: "230px",
    minHeight: "230px",
    maxWidth: "230px",
    maxHeight: "230px",

    [theme.breakpoints.down(768)]: {
      marginBottom: 16,
    },
  },

  items: {
    display: "flex",
    [theme.breakpoints.down(768)]: {
      flexDirection: "column",
      alignItems: "center",
    },
  },

  box: {
    marginLeft: 16,
    [theme.breakpoints.down("768")]: {
      margin: 0,
    },
  },

  button: {
    width: "100%",
    height: 56,
    displat: "block",
    marginTop: 16,
  },

  title: {
    fontWeight: "600",
    display: "flex",
    alignItems: "center",
    marginBottom: 16,
    fontSize: 23,
  },

  subtitle: {
    lineHeight: 1,
    marginBottom: 16,
    fontWeight: "400",
    color: "#64748B",
  },

  code: {
    width: "50%",
    textDecoration: "underline",
    display: "flex",
    justifyContent: "center",
    fontSize: 20,
    lineHeight: "24px",
    color: "#000",
    fontWeight: 600,
    flex: 1,
    [theme.breakpoints.down("sm")]: {
      marginBottom: 16,
    },
    [theme.breakpoints.down(768)]: {
      width: "100%",
    },
  },

  SecurityInput: {
    background: "#F7F8F9",
    boxShadow: "0px 1px 0px #E2E8F0",
    marginTop: 20,
  },

  buttonCopy: {
    width: "50%",
    [theme.breakpoints.down(768)]: {
      width: "100%",
    },
  },
  innerItems: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    columnGap: 16,
    [theme.breakpoints.down("sm")]: {
      flexDirection: "column",
      rowGap: "16px",
    },
  },
}));
