/* eslint-disable eqeqeq */
import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  makeStyles,
  Snackbar,
  Typography,
} from "@material-ui/core";
import { Alert } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import googleAuthenticator from "../../../../assets/images/2fa.png";
import {
  editSecurityParameters,
  setChangedSecurityParameters,
} from "../../../../redux/reducers/myProfile";

import DialogDisable from "./DialogDisable";

const Edit = ({ t }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);
  const [auth, setAuth] = useState(null);
  const [transfer, setTransfer] = useState(null);
  const [cashout, setCashout] = useState(null);
  const [values, setValues] = useState({});

  const { securityParameters, changedParams } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  useEffect(() => {
    if (securityParameters?.data?.security["2fa"]) {
      setAuth(securityParameters?.data?.security["2fa"].auth);
      setCashout(securityParameters?.data?.security["2fa"].cashout);
      setTransfer(securityParameters?.data?.security["2fa"].transfer);
    }
  }, [securityParameters]);

  const toggleCheckbox = (event) => {
    if (event.target.attributes.name.value == "auth")
      setAuth(event.target.checked);
    if (event.target.attributes.name.value == "transfer")
      setTransfer(event.target.checked);
    if (event.target.attributes.name.value == "cashout")
      setCashout(event.target.checked);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setOpen(true);

    dispatch(
      editSecurityParameters({
        auth: auth.toString(),
        transfer: transfer.toString(),
        cashout: cashout.toString(),
      })
    );
    setValues({
      auth: auth.toString(),
      transfer: transfer.toString(),
      cashout: cashout.toString(),
    });
  };

  return (
    <Box className={classes.wrapper}>
      <Snackbar
        open={changedParams?.open}
        autoHideDuration={3000}
        onClose={() =>
          dispatch(
            setChangedSecurityParameters({ ...changedParams, open: false })
          )
        }
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          severity={changedParams?.severity === 200 ? "success" : "error"}
          color={changedParams?.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {changedParams?.message?.messages
            ? changedParams?.message?.messages
            : t("successfullyChanged")}
        </Alert>
      </Snackbar>

      <div
        style={{
          borderBottom: "1px solid #E7EAEE",
          paddingBottom: 24,
          marginBottom: 24,
        }}
      >
        <Typography variant="h5" className={classes.title}>
          <img src={googleAuthenticator} className={classes.img} alt="" />
          Google Authenticator
        </Typography>
        <Typography
          variant="h6"
          style={{ fontWeight: "400", color: "#64748B", lineHeight: 1 }}
        >
          {t("f2aSubtitlee")}
        </Typography>
      </div>

      <form onSubmit={handleSubmit}>
        <Box display="flex" flexDirection="column">
          <FormControlLabel
            control={
              <Checkbox
                name="auth"
                onChange={toggleCheckbox}
                value={auth}
                checked={auth}
                color="primary"
              />
            }
            label={t("auth")}
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={cashout}
                name="cashout"
                value={cashout}
                onChange={toggleCheckbox}
                color="primary"
              />
            }
            label={t("cashout")}
          />
          <FormControlLabel
            control={
              <Checkbox
                name="transfer"
                checked={transfer}
                value={transfer}
                onChange={toggleCheckbox}
                color="primary"
              />
            }
            label={t("transfer")}
          />
          <Button
            size="large"
            color="secondary"
            type="submit"
            style={{ marginTop: 20 }}
            variant="contained"
          >
            {t("Save")}
          </Button>
        </Box>
      </form>

      <DialogDisable setOpen={setOpen} edit data={values} open={open} t={t} />
    </Box>
  );
};

export default Edit;

const useStyles = makeStyles((theme) => ({
  wrapper: {
    background: "#FFFFFF",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: 8,
    padding: 32,
    color: "#000",
    [theme.breakpoints.down("sm")]: {
      padding: 16,
    },
  },
  title: {
    fontWeight: "600",
    display: "flex",
    alignItems: "center",
    marginBottom: 16,
  },
  img: {
    width: 40,
    height: 40,
    marginRight: 16,
  },
}));
