/* eslint-disable eqeqeq */
import React, { useRef, useState } from "react";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogContent,
  IconButton,
  makeStyles,
  TextField,
  Typography,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import close from "../../../../assets/images/state/close.svg";
import { Form, Formik } from "formik";
import { disable2fa } from "../../../../redux/reducers/2fa";
import { useLayoutEffect } from "react";
import {
  editSecurityParameters,
  getProfileData,
  getSecurityParametrs,
} from "../../../../redux/reducers/myProfile";
import { MaskCustom } from "../../../../components/inputMask/unicalMask";

const DialogDisable = ({ t, data, open, setOpen, edit }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [loading, setLoading] = useState(false);

  const { response } = useSelector(({ f2aReducer }) => f2aReducer);
  const { changedParams } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (response?.severity == 200)
      setTimeout(() => {
        setOpen(false);
        dispatch(getSecurityParametrs());
      }, 3000);
    else setLoading(false);
    // eslint-disable-next-line 
  }, [response]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (changedParams?.severity == 200)
      setTimeout(() => {
        setOpen(false);
        dispatch(getProfileData());
      }, 3000);
    else setLoading(false);
    // eslint-disable-next-line 
  }, [changedParams]);

  return (
    <Dialog
      open={open}
      classes={{ paper: classes.paper }}
      onClose={() => setOpen(false)}
      scroll="body"
    >
      <DialogContent className={classes.dialog}>
        <IconButton
          aria-label="close"
          onClick={() => setOpen(false)}
          className={classes.close}
        >
          <img src={close} alt="" />
        </IconButton>
        <Typography variant="h4" className={classes.title}>
          {t("f2a")}
        </Typography>
        <Typography variant="body1" className={classes.subtitle}>
          {t("f2aSubtitle")}
        </Typography>
        <Formik
          initialValues={{
            "2fa_otp": "",
          }}
          onSubmit={(values) => {
            setLoading(true);
            edit
              ? dispatch(
                  editSecurityParameters({
                    ...data,
                    "2fa_otp": values["2fa_otp"],
                  })
                )
              : dispatch(disable2fa(values));
          }}
        >
          {({ values, handleChange }) => (
            <Form style={{ width: "100%" }}>
              <TextField
                name="2fa_otp"
                onChange={handleChange}
                value={values["2fa_otp"]}
                className={classes.input}
                label={t("LabelSecurityCode")}
                variant="outlined"
                type="number"
                required
                id={"000000"}
                InputProps={{
                  inputComponent: MaskCustom,
                }}
              />
              <Button
                variant="contained"
                size="large"
                color="primary"
                type="submit"
                disabled={loading}
                className={classes.button}
              >
                {loading ? (
                  <CircularProgress color="secondary" />
                ) : (
                  t("continue")
                )}
              </Button>
            </Form>
          )}
        </Formik>
      </DialogContent>
    </Dialog>
  );
};

export default DialogDisable;

const useStyles = makeStyles((theme) => ({
  paper: {
    width: "100%",
    borderRadius: 16,
    padding: "40px 19px 50px!important",
  },
  dialog: {
    overflow: "hidden",
    display: "flex",
    margin: "0 auto",
    padding: "0!important",
    justifyContent: "center",
    maxWidth: "90%",
    alignItems: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },
  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },
  title: {
    textAlign: "center",
    lineHeight: "1",
    marginBottom: 10,
    fontSize: 32,
  },

  subtitle: {
    color: "#777",
    marginBottom: 24,
    fontWeight: "400",
  },

  input: {
    background: "#F7F8F9",
    boxShadow: "0px 1px 0px #E2E8F0",
    marginTop: 20,
  },

  button: {
    width: "100%",
    height: 56,
    marginTop: 16,
  },
}));
